import React, {
  useState,
  useEffect,
  SyntheticEvent,
  ChangeEvent,
  FC,
} from 'react';

import classnames from 'classnames/bind';
import { Helmet } from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';
import { useSnackbar } from 'notistack';

import TopLoading from 'components/TopLoading';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import InputAdornment from '@material-ui/core/InputAdornment';
import IconButton from '@material-ui/core/IconButton';
import { FaEyeSlash, FaEye } from 'react-icons/fa';
import CircularProgress from '@material-ui/core/CircularProgress';
import { loginRequest } from 'store/auth/actions';

import validate, { ValidatorNames } from 'utils/validation';
import { FormStateType, FormFieldType, ValidationRuleType } from 'types/common';
import { ReactComponent as Logo } from 'assets/logo.svg';
import { AppStateType } from 'types';
import styles from './styles.module.scss';

const cx = classnames.bind(styles);

const LoginPage: FC = () => {
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const {
    isPending,
    error,
  } = useSelector((state: AppStateType) => state.auth);

  const fields = {
    username: 'username',
    password: 'password',
  };

  const [formData, setFormData] = useState<FormStateType>({
    [fields.username]: {
      value: '',
      validationMessage: null,
    },
    [fields.password]: {
      value: '',
      validationMessage: null,
    },
  });

  const validation: { [key: string]: ValidationRuleType[] } = {
    [fields.username]: [
      { type: ValidatorNames.required },
      { type: ValidatorNames.emailOrPhoneNumber },
    ],
    [fields.password]: [
      { type: ValidatorNames.required },
      {
        type: ValidatorNames.minLength,
        length: 8,
      },
    ],
  };

  const [formIsValid, setFormIsValid] = useState(false);
  const [submitBtnClicked, setSubmitBtnClicked] = useState(false);

  useEffect(() => {
    let valid = true;

    for (const index in formData) {
      if (formData[index].validationMessage) {
        valid = false;
        break;
      }
    }

    if (formIsValid !== valid) {
      setFormIsValid(valid);
    }
  }, [formData]);

  useEffect(() => {
    if (error?.detail) {
      for (const key in error?.detail) {
        enqueueSnackbar(error.detail[key], { variant: 'error' });
      }
    }
  }, [error]);

  const [showPassword, setShowPassword] = useState(false);

  const onFormDataChange = (fieldName: string) => (event: ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [fieldName]: {
        value: event.target.value,
        validationMessage: validate(validation[fieldName], event.target.value),
      },
    });
  };

  const handleSubmit = (event: SyntheticEvent) => {
    event.preventDefault();
    let valid = true;

    if (!submitBtnClicked) {
      const newFormData = {};

      for (const key in fields) {
        const validRes = validate(validation[key], formData[key].value);

        newFormData[key] = {
          ...formData[key],
          validationMessage: validRes,
        };

        if (validRes) {
          valid = false;
        }
      }

      setFormData(newFormData);

      setSubmitBtnClicked(true);
    }

    if (formIsValid && valid) {
      dispatch(loginRequest({
        username: formData[fields.username].value,
        password: formData[fields.password].value,
      }));
    }
  };

  const handleClickOnShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const getFieldProps = (fieldName: string) => {
    const fieldProps: FormFieldType = {
      onChange: onFormDataChange(fieldName),
      value: formData[fieldName].value,
      hasError: false,
      helperText: null,
    };

    if (submitBtnClicked) {
      fieldProps.hasError = !!formData[fieldName].validationMessage;
      fieldProps.helperText = formData[fieldName].validationMessage;
    }

    return fieldProps;
  };

  const usernameProps = getFieldProps(fields.username);
  const passwordProps = getFieldProps(fields.password);

  return (
    <>
      <Helmet>
        <title>Novakid - Sign in</title>
      </Helmet>
      <TopLoading loading={false} />
      <div className={cx('loginPage')}>
        <form
          onSubmit={handleSubmit}
          className={styles.form}
          noValidate
          autoComplete="off"
        >
          <div className={cx('logo')}>
            <Logo />
          </div>
          <TextField
            label="Email or phone number"
            className={cx({ hasError: usernameProps.hasError })}
            variant="outlined"
            type="email"
            size="small"
            required
            {...usernameProps}
          />
          <TextField
            label="Password"
            variant="outlined"
            className={cx({ hasError: passwordProps.hasError })}
            size="small"
            type={showPassword ? 'text' : 'password'}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton onClick={handleClickOnShowPassword}>
                    {showPassword ? <FaEye /> : <FaEyeSlash />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
            required
            {...passwordProps}
          />
          <Button
            type="submit"
            variant="contained"
            color="primary"
            size="medium"
            disabled={isPending}
          >
            {isPending ? <CircularProgress size={21} /> : 'Sign in'}
          </Button>
        </form>
      </div>
    </>
  );
};

export default LoginPage;
