import {
  ValidatorType,
  ValidationRuleType,
} from 'types/common';

import isEmail from 'validator/es/lib/isEmail';
import isMobilePhone from 'validator/es/lib/isMobilePhone';
import equals from 'validator/es/lib/equals';

export const ValidatorNames = {
  required: 'required',
  minLength: 'minLength',
  email: 'email',
  phoneNumber: 'phoneNumber',
  emailOrPhoneNumber: 'emailOrPhoneNumber',
  equals: 'equals',
};

const validators: { [key: string]: ValidatorType } = {
  [ValidatorNames.required]: (rule, value) => {
    const defaultMessage = 'The field can be empty';

    if (value) {
      return null;
    }

    return rule.message ? rule.message : defaultMessage;
  },
  [ValidatorNames.minLength]: (rule, value) => {
    if (!rule.length) {
      return null;
    }

    const defaultMessage = `Min amount of symbols: ${rule.length}`;

    if (value.length >= rule.length) {
      return null;
    }

    return rule.message ? rule.message : defaultMessage;
  },
  [ValidatorNames.email]: (rule, value) => {
    const defaultMessage = 'Incorrect e-mail';

    if (isEmail(value)) {
      return null;
    }

    return rule.message ? rule.message : defaultMessage;
  },
  [ValidatorNames.phoneNumber]: (rule, value) => {
    const defaultMessage = 'Incorrect phone number';

    if (isMobilePhone(value)) {
      return null;
    }

    return rule.message ? rule.message : defaultMessage;
  },
  [ValidatorNames.emailOrPhoneNumber]: (rule, value) => {
    const defaultMessage = 'Enter correct phone number or email';

    if (isMobilePhone(value) || isEmail(value)) {
      return null;
    }

    return rule.message ? rule.message : defaultMessage;
  },
  [ValidatorNames.emailOrPhoneNumber]: (rule, value) => {
    const defaultMessage = 'Enter correct phone number or email';

    if (isMobilePhone(value) || isEmail(value)) {
      return null;
    }

    return rule.message ? rule.message : defaultMessage;
  },
  [ValidatorNames.equals]: (rule, value) => {
    const defaultMessage = 'The fiedls do not match';

    if (rule.compareTo && equals(value, rule.compareTo)) {
      return null;
    }

    return rule.message ? rule.message : defaultMessage;
  },
};

export default function validate(
  rules: ValidationRuleType[],
  value: string,
): null | string {
  for (const rule of rules) {
    const validator = validators[rule.type];

    if (!validator) {
      throw Error(`validator '${rule.type}' is not existed`);
    }

    const message = validator(rule, value);

    if (message) {
      return message;
    }
  }

  return null;
}
